.header {
    $block: &;
    position: relative;
    background-color: $color-black;
    width: 100vw;
    height: 100vh;

    &__top {
        position: relative;
        border-bottom-left-radius: $border-radius;
        border-bottom-right-radius: $border-radius;
        overflow: hidden;
        height: 100%;
        width: 100%;
        margin: 0 auto;
    }

    &__bg {
        @include fillout;
        z-index: 0;

        &__video-wrapper {
            @include fillout;
            z-index: 1;

            &::before {
                content: '';
                display: block;
                @include fillout;
                background-color: rgba($color-black, 0.2);
                z-index: 2;
            }

            video {
                @include fillout;
                @include object-fit(cover);
            }
        }

        &__image {
            img {
                @include fillout;
                @include object-fit(cover);
            }
        }
    }

    &__caption {
        position: absolute;
        z-index: 1;
        width: 100%;
        text-align: center;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        transition: $transition-link;
    }

    &__uptitle {
        margin-bottom: 20px;
        transition: $transition-link;

        &.is-hidden {
            opacity: 0;
        }

        @include mq($until: tablet) {
            line-height: 140%;
        }
    }

    &__title {
        @include mq($until: tablet) {
            font-size: 56px;
            line-height: 110%;
        }
    }

    &__bottom {
        position: absolute;
        bottom: 0;
        display: flex;
        padding: 32px 40px;
        justify-content: space-between;
        align-items: center;
        pointer-events: none;

        @include mq($until: 'tablet-landscape') {
            justify-content: center;
        }

        &__caption,
        &__link {
            opacity: 1;
            transition: $transition-link;

            &.is-hidden {
                opacity: 0;
            }
        }
    }
}
