.partners {
    $block: &;

    padding: toRem(350) 0;
    background-color: $color-grey-darker;
    border-radius: 6px;
    transform: translateY(-6px);

    @include mq($until: tablet) {
        padding: toRem(200) 0;
    }

    &__gallery {
        display: flex;

        &__inner {
            display: flex;
        }

        &__icon {
            padding: 0 toRem(50);
        }
    }
}
