[data-scrollbar] {
  height: 100vh;
  width: 100vw;
  overflow: auto;
  background: #fff;
  /* Hide scrollbar for IE, Edge and Firefox */
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
  margin-bottom: -1px;
}

.scroll-content {
  overflow: hidden !important;
  width: 100vw;
  will-change: transform;
  /* Hide scrollbar for IE, Edge and Firefox */
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
}

.scrollbar-track {
  background: 0 0 !important;
  z-index: 200 !important;
  mix-blend-mode: difference;
}

.scrollbar-thumb-y {
  right: 0 !important;
  width: 5px !important;
  background: #fff !important;
  border-radius: 6px !important;
}
.is-locked .scrollbar-thumb-y {
  pointer-events: none;
  opacity: 0;
}

.scrollbar-thumb-y:hover {
  cursor: grab;
}

.button {
  display: inline-block;
  align-items: center;
  text-align: center;
  cursor: pointer;
  border: none;
  border-radius: 6px;
  position: relative;
  text-transform: uppercase;
  line-height: 1;
  font-weight: 700;
  transition: 0.2s cubic-bezier(0, 0, 0.58, 1);
}
.button.is-disabled {
  cursor: not-allowed;
  pointer-events: none;
}
.button > .preloader {
  position: absolute;
  top: 50%;
  transform: translateY(-50%) translateZ(0);
  right: 20px;
}
.button--style--white {
  background-color: #fff;
  color: black;
}
.no-touch .button--style--white:hover {
  background-color: #e6e6e6;
}
.button--style--black {
  background-color: #000;
  color: white;
}
.no-touch .button--style--black:hover {
  background-color: #1a1a1a;
}
.button--size--default {
  font-size: 16px;
  padding: 22px 20px;
}
.button__inner {
  display: flex;
  align-items: center;
}

.navigation {
