.overlay {
  min-height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  left: 0;
  top: 0;
  padding: 3.75rem 0;
  z-index: 120;
  transform: translateY(100%);
  pointer-events: none;
  transition: 1s cubic-bezier(0.19, 1, 0.22, 1);
}
.overlay.is-visible {
  pointer-events: all;
  transform: none;
}
.overlay > .preloader {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) translateZ(0);
}
.overlay__content {
  background-color: #000;
  width: 100%;
  box-shadow: 0 10px 40px 0 rgba(0, 0, 0, 0.2);
  position: relative;
  height: calc(100vh - 60px);
  overflow: scroll;
  /* Hide scrollbar for IE, Edge and Firefox */
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
}
.overlay__content--default {
  max-width: 900px;
}
.overlay__content--full-hd {
  max-width: 1920px;
}
.overlay__content--transparent {
  box-shadow: none;
  background-color: transparent;
}
.overlay__content:focus {
  outline: none;
}
.overlay__close {
  transition: 0.2s cubic-bezier(0, 0, 0.58, 1);
  position: absolute;
  width: 100%;
  text-align: right;
  top: 0;
  right: 0;
  padding: 1em;
  font-size: 16px;
  cursor: pointer;
  z-index: 10;
  opacity: 0;
  height: 3.75rem;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
@media (max-width: 23.49em) {
  .overlay__close {
    justify-content: center;
    text-align: center;
    left: 50%;
    transform: translate(-50%, 0);
  }
}
.overlay__close svg {
  pointer-events: none;
}
.is-visible .overlay__close {
  opacity: 1;
  transition-delay: 0.7s;
}
.no-touch .overlay__close:hover {
  color: #e6e6e6;
}

@keyframes hideOverlayContent {
  from {
    opacity: 1;
    transform: none;
  }
  to {
    opacity: 0;
    transform: translateY(5px);
  }
}
.page {
  overflow: hidden;
}
.page::after {
  content: "";
  z-index: 100;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  opacity: 0;
  visibility: hidden;
  background-color: rgba(0, 0, 0, 0.9);
  transition: opacity, visibility, z-index;
  transition-delay: 0s, 0s, 0.4s;
  transition-duration: 0.4s, 0.4s, 0.01s;
  pointer-events: none;
}
.is-dimmed .page::after {
  visibility: visible;
  pointer-events: auto;
  opacity: 1;
}
.is-locked .page {
  position: fixed;
  left: 0;
  width: 100%;
}

.preloader {
  position: fixed;
  width: 100vw;
  height: 100vh;
  z-index: 11;
  background-color: #000;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.preloader__title {
  opacity: 1;
}
.preloader .blink {
  animation: blinker 0.25s linear infinite;
}
@keyframes blinker {
  50% {
    opacity: 0;
  }
}

