.team {
    background-color: $color-black;
    padding: toRem(350) 0 toRem(350) toRem(40);
    display: flex;
    align-items: center;
    flex-direction: row;
    cursor: grab;

    @include mq($until: tablet) {
        flex-direction: column;
        align-items: flex-start;
        padding: toRem(150) 0 toRem(150) toRem(24);
    }

    &__title {
        position: absolute;
        left: 40px;
        opacity: 0;
        pointer-events: none;

        @include mq($until: tablet) {
            position: inherit;
            top: 0;
            left: 0;
            margin: 0 0 toRem(60) 0;
        }
    }

    &__gallery {
        user-select: none;
        display: flex;
        align-items: center;
        margin-left: auto;
        margin-right: auto;
        overflow: visible;
        width: 50%;

        @include mq($until: tablet) {
            width: 100%;
        }

        &__inner {
            display: flex;
            padding-left: 30vw;

            @include mq($until: tablet) {
                padding: 0;
            }
        }

        &__item {
            position: relative;
            margin-right: toRem(30);
            pointer-events: none;
            overflow: hidden;
            border-radius: toRem(6);
            width: toRem(300);
            height: toRem(400);

            @include mq($until: tablet) {
                width: toRem(300);
                height: toRem(400);
            }

            @include mq($until: 'mobile-landscape') {
                margin-right: toRem(10);
            }

            &__image {
                @include fillout;

                &::before {
                    @include fillout;
                    content: '';
                    background-color: rgba(0, 0, 0, 0.2);
                }
            }

            &__text {
                position: absolute;
                bottom: toRem(30);
                left: toRem(30);
            }
        }
    }
}
