.video-player {
    $block: &;

    will-change: height;
    transition: height 0.5s $ease-out-expo;

    // hide controls
    video::-webkit-media-controls-enclosure {
        display: none !important;
    }
    video::-webkit-media-controls {
        display: none;
    }

    &__video {
        position: absolute;
        top: 0;
        left: 0;

        width: 100vw;
        height: 56.25vw; /* Given a 16:9 aspect ratio, 9/16*100 = 56.25 */
        min-height: 100%;
        min-width: 177.77vh; /* Given a 16:9 aspect ratio, 16/9*100 = 177.77 */
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        &-wrapper {
            @include fillout;
            width: 100%;
            z-index: 3;
        }

        &__button {
            position: absolute;
            display: flex;
            align-items: center;
            opacity: 0;
            pointer-events: none;
            transition: $transition-link;

            max-height: toRem(60);
            bottom: toRem(21);
            right: toRem(21);

            .is-video-playing & {
                opacity: 1;
                pointer-events: all;
            }
            img {
                @include object-fit(contain);
                margin-right: toRem(23);
                width: toRem(23.5);
                height: toRem(30);
            }
        }
    }

    &__controls {
        position: absolute;
        display: flex;
        bottom: 21px;
        left: 21px;
        max-height: toRem(60);
        opacity: 0;
        transition: $transition-link;

        .is-video-playing & {
            opacity: 1;
        }

        &__duration,
        &__mute,
        &__return {
            display: flex;
            cursor: pointer;
            align-items: center;
            padding: 20px;
            border-radius: $border-radius;
            background-color: $color-white;
        }

        &__mute {
            margin-left: toRem(10);
            &-off,
            &-on {
                font-size: 20px;
                height: 20px;
                display: flex;

                svg {
                    fill: none;
                    stroke: $color-black;
                }
            }

            &-off {
                opacity: 0.5;
                display: none;

                .is-muted & {
                    display: flex;
                }
            }

            &-on {
                .is-muted & {
                    display: none;
                }
            }
        }

        &__return {
            margin-right: toRem(10);
            &-button {
                font-size: 10px;
                height: 20px;
                display: flex;

                svg {
                    fill: none;
                    stroke: $color-black;
                }
            }
        }

        &__play,
        &__pause {
            font-size: 10px;
            height: 20px;
            display: flex;
            margin-right: toRem(30);

            svg {
                fill: none;
                stroke: $color-black;
            }
        }

        &__time {
            display: flex;
            align-items: center;

            &--total {
                opacity: 0.5;
            }
        }
    }

    &__bg-img {
        z-index: 4;
        transition: opacity 0.4s $ease-out-quart;
    }
    &__bg-vid {
        z-index: 5;
        transition: opacity 0.4s $ease-out-quart;
    }

    // MODS
    &[data-playstate='playing'],
    &[data-playstate='paused'] {
        height: 100% !important;
        #{$block} {
            &__bg-img,
            &__bg-vid {
                opacity: 0;
                pointer-events: none;
            }
        }
        .header__caption {
            opacity: 0;
            pointer-events: none;
        }
    }

    &[data-playstate='playing'] {
        #{$block}__controls__play {
            display: none;
        }
    }
    &[data-playstate='paused'],
    &[data-playstate='closed'] {
        #{$block}__controls__pause {
            display: none;
        }
        #{$block}__controls__play {
            display: flex;
        }
    }
}
