.typeform {
    $block: &;
    padding: toRem(100) toRem(24) 0;
    position: relative;
    display: flex;
    align-items: flex-start;
    height: calc(100vh - 60px); //60px topbar
    width: 100%;
    overflow-y: scroll;

    &__container {
        width: 100%;
        margin: 0 auto; //to center
        height: 100%;

        @include mq(tablet-landscape) {
            max-width: toRem(660);
        }
    }

    &__wrapper {
        position: absolute;
        opacity: 0;
        display: flex;
        justify-content: center;
        flex-direction: row;
        align-items: flex-start;
        height: 100%;
        width: 100%;
        transition: 0.2s $ease-out;
        transition-delay: 0s;
        z-index: -1;

        .is-form-opened & {
            transition-delay: 0.25s;
            transform: none;
            opacity: 1;
            z-index: 1;
        }

        .is-form-success & {
            transition-delay: 0s;
            transform: none;
            opacity: 0;
            z-index: -1;
        }
    }

    @include mq($until: tablet-landscape) {
        flex-direction: column;
        align-items: center;
        padding: toRem(40) toRem(24) toRem(15);
    }

    &__button {
        position: sticky;
        top: 0;
        left: 0;
        letter-spacing: 0.05em;
        border: 1px solid $color-white;
        border-radius: toRem(6);
        margin: 0;

        @include mq($until: tablet-landscape) {
            position: static;
            display: block;
            width: fit-content;
            margin-bottom: toRem(100);
            align-self: flex-start;
        }
    }

    &__title {
        line-height: 100%;
        padding-bottom: toRem(100);
    }

    &__field {
        margin-bottom: toRem(80);

        &__header {
            display: flex;
            justify-content: space-between;
        }

        &__label {
            color: $color-grey-dark;
            line-height: 1;
            padding-bottom: toRem(20);

            @include mq($until: mobile-landscape) {
                font-size: 14px;
            }
        }

        &__error-label {
            transition: $transition-link;

            .is-error & {
                color: $color-alert;
            }
        }

        &__input {
            background: transparent;
            border: none;
            border-bottom: 1px solid $color-grey-dark;
            color: $color-white;
            padding: toRem(22) 0;
            line-height: 1;
            width: 100%;
            transition: $transition-link;

            &:focus {
                border-color: $color-white;
            }

            &::placeholder {
                color: $color-grey-dark;
                transition: $transition-link;
            }

            &.is-error {
                border-color: $color-alert;

                & + {
                    #{$block}__field__error-label {
                        opacity: 1;
                    }
                }

                &::placeholder {
                    opacity: 1;
                    color: $color-alert;
                }
            }
        }

        &__select {
            appearance: none;
            background: $color-black;
            border: 1px solid $color-silver;
            border-radius: toRem(6);
            padding: toRem(22) toRem(20);
            width: 100%;
            line-height: 1;
        }

        &__accordion {
            position: absolute;
            display: flex;
            flex-direction: column;
            background-color: $color-black;
            border: 1px solid $color-silver;
            border-radius: toRem(6);
            color: $color-white;
            width: 100%;
            line-height: 1;
            cursor: pointer;

            &__header {
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: toRem(22) toRem(20);

                &__item.is-selected {
                    display: flex;
                    justify-content: space-between;
                    width: 100%;
                    pointer-events: none;
                }

                &__item {
                    display: none;

                    &__title {
                        letter-spacing: 0.05em;
                    }

                    &__icon {
                        position: relative;

                        &--opened {
                            position: absolute;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            transition: all 0.2s $ease;
                            right: 0;

                            opacity: 0;
                        }

                        &--closed {
                            position: absolute;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            transition: all 0.2s $ease;
                            right: 0;

                            opacity: 1;
                        }

                        .is-open &--opened {
                            opacity: 1;
                        }

                        .is-open &--closed {
                            opacity: 0;
                        }
                    }
                }
            }

            &__content {
                &__wrapper {
                    max-height: 0;
                    transition: all 0.2s $ease;
                    overflow: hidden;
                }

                &__text {
                    line-height: 1;
                    letter-spacing: 0.05em;
                    padding: toRem(22) toRem(20);
                    opacity: 0.7;
                }

                &__text.is-selected {
                    display: none;
                }
            }
        }

        &__options {
            display: flex;
            flex-wrap: wrap;
            margin-top: -5px;
            margin-left: -5px;
            overflow: hidden;

            @include mq($until: mobile-landscape) {
                flex-wrap: nowrap;
                display: -webkit-box;
                overflow-x: scroll;
            }

            &__item {
                padding: toRem(22) toRem(20);
                border-radius: 6px;
                margin: toRem(5);
                color: $color-grey-dark;
                cursor: pointer;
                border: 1px solid transparent;
                transition: $transition-link;
                line-height: 1;
            }

            &__item.is-active {
                border-color: $color-silver;
                color: $color-white;
            }
        }

        &--phone {
            margin-bottom: toRem(60);
        }

        &--email {
            margin-bottom: toRem(100);
        }

        &--select {
            position: relative;
            margin-bottom: toRem(120);
        }

        &--multiselect {
            transition: all 0.4s $ease;
            overflow: hidden;
            opacity: 1;
            max-height: 200px;
        }
    }

    &__send {
        width: 100%;
        margin-bottom: toRem(200);

        @include mq($until: mobile-landscape) {
            margin-bottom: toRem(15);
        }
    }

    &__send.is-disabled {
        background-color: $color-grey-dark;
    }

    &__accordion-mobile-overlay {
        position: fixed;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.8);
        z-index: -10;
        pointer-events: none;
    }

    &__accordion-mobile {
        position: fixed;
        width: 100%;
        background: #1e1e1e;
        padding: toRem(40) 0 toRem(80);
        z-index: 2;
        bottom: 0;
        transform: translate(0, 100%);

        &__title {
            color: #8b9094;
            margin-left: toRem(24);
        }

        &__items {
            margin-top: toRem(40);
        }

        &__item {
            padding: toRem(22) toRem(24);
            display: flex;
            justify-content: space-between;
            color: #8b9094;
            line-height: 1;

            &__label {
                pointer-events: none;
            }

            .is-selected &__label {
                color: $color-white;
                pointer-events: none;
            }

            &__icon {
                display: none;
                pointer-events: none;
            }

            .is-selected &__icon {
                display: block;
                pointer-events: none;
            }
        }
    }

    &__success {
        $block: &;
        padding: toRem(100) toRem(24) 0;
        position: relative;
        display: flex;
        align-items: flex-start;
        height: calc(100vh - 60px); //60px topbar
        width: 100%;
        overflow-y: scroll;

        @include mq($until: 'mobile-landscape') {
            padding-top: toRem(80);
        }

        &__wrapper {
            position: absolute;
            display: flex;
            justify-content: center;
            flex-direction: row;
            align-items: flex-start;
            height: 100%;
            width: 100%;
            transition: 0.2s $ease-out;
            transition-delay: 0s;
            z-index: -1;
            opacity: 0;

            .is-form-opened & {
                transition-delay: 0s;
                transform: none;
                opacity: 0;
                z-index: -1;
            }

            .is-form-success & {
                transition-delay: 0.25s;
                transform: none;
                opacity: 1;
                z-index: 1;
            }
        }

        &__container {
            max-width: toRem(660);
            width: 100%;
            margin: 0 auto; //to center
            // overflow-y: scroll;
            height: 100%;
        }

        &__title {
        }

        &__subtitle {
            margin: toRem(100) 0 toRem(60);
            font-weight: 700;

            @include mq($until: 'mobile-landscape') {
                margin-top: toRem(80);
            }
        }

        &__calendar {
            display: flex;
            justify-content: space-between;
            flex-direction: column;

            @include mq($until: 'mobile-landscape') {
                justify-content: flex-end;
                flex-direction: row-reverse;
            }

            &__events {
                display: flex;
                justify-content: space-between;
                flex-direction: row;

                @include mq($until: 'mobile-landscape') {
                    flex-direction: column;
                }

                &__item {
                    line-height: 1;

                    &__title {
                        margin-bottom: toRem(20);
                    }

                    &__date {
                        color: $color-grey-dark;
                    }
                }
            }

            &__grid {
                display: flex;
                flex-direction: column;
                margin-top: toRem(40);
                margin-right: 0;
                height: auto;

                @include mq($until: 'mobile-landscape') {
                    flex-direction: row;
                    height: toRem(350);
                    margin-top: 0;
                    margin-right: toRem(30);
                }

                &__row {
                    position: relative;
                    display: flex;
                    justify-content: flex-end;

                    @include mq($until: 'mobile-landscape') {
                        flex-direction: column;
                    }

                    &--middle {
                        display: flex;
                        justify-content: flex-end;
                        margin: toRem(4) 0 toRem(4);

                        @include mq($until: 'mobile-landscape') {
                            flex-direction: column;

                            margin: 0 toRem(4) 0 toRem(4);
                        }
                    }

                    &__cell {
                        height: toRem(4);
                        background-color: $color-grey-darker;

                        &--big {
                            display: flex;
                            height: toRem(4);
                            background-color: $color-grey-darker;
                            width: 100%;

                            @include mq($until: 'mobile-landscape') {
                                flex-direction: row;
                                width: toRem(4);
                                height: 100%;
                            }
                        }

                        &--small {
                            height: toRem(4);
                            background-color: $color-grey-darker;
                            width: 33%;

                            @include mq($until: 'mobile-landscape') {
                                flex-direction: row;
                                width: toRem(4);
                                height: 33%;
                            }
                        }

                        &--middle {
                            height: toRem(4);
                            background-color: $color-grey-darker;
                            margin: 0 toRem(2) 0 toRem(2);
                            width: 33%;

                            @include mq($until: 'mobile-landscape') {
                                flex-direction: row;
                                width: toRem(4);
                                height: 33%;
                                margin: toRem(2) 0 toRem(2);
                            }
                        }

                        &--indicator {
                            position: absolute;
                            width: 12%;
                            height: toRem(4);
                            background-color: $color-white;

                            @include mq($until: 'mobile-landscape') {
                                flex-direction: row;
                                width: toRem(4);
                                height: 12%;
                            }
                        }
                    }
                }
            }
        }

        &__button {
            width: 100%;
            margin: toRem(80) 0 0;

            @include mq($until: mobile-landscape) {
                margin: toRem(95) 0 toRem(15);
            }
        }
    }
}
