$sequenceWidth: 1315px;
$sequenceHeight: 740px;

.intro {
    $block: &;

    position: relative;
    background-color: $color-silver;
    padding-bottom: toRem(350);

    @include mq($until: tablet) {
        padding-bottom: toRem(150);
    }

    // whole scene black + silver
    &__scene {
        position: relative;
        height: calc(720vh + 200px);
    }

    // black scene
    &__black {
        display: flex;
        flex-direction: column;
        position: relative;
        background-color: $color-black;
        height: 500vh;
        overflow: hidden;

        // container which iwll be pinned
        &__captions {
            position: relative;
            height: 300vh;
            text-align: center;
            width: 100%;
            max-width: 1160px;
            margin: 0 auto;
            z-index: 10;

            #{$block}__black__uptitle {
                margin-bottom: toRem(20);
            }

            // inner slides to be absolute
            #{$block}__black__title {
                position: absolute;
                width: 100%;
                left: 50%;
                top: 16.66666%;
                transform: translate(-50%, -50%);
            }

            @include mq($until: tablet) {
                top: 0;
            }
        }
    }

    &__sequence {
        position: relative;
        height: 100%;

        &__container {
            position: relative;
            height: 100vh;
        }
        &__stage {
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;

            canvas {
                width: $sequenceWidth;
                height: $sequenceHeight;

                @include mq($until: tablet) {
                    display: none;
                }
            }

            &__image {
                width: 100%;
                height: 100%;
                img {
                    @include fillout;
                    @include object-fit(contain);
                }
                @include mq(tablet) {
                    display: none;
                }
            }
        }
    }

    &__silver {
        // height: 110vh;
        overflow: hidden;
        padding-bottom: toRem(50);

        clip-path: polygon(0 0, 100% 2%, 100% 100%, 0% 100%);
        margin-top: -5.55vh;

        &__content {
            position: relative;
            margin-top: -100vh;
            height: 100vh;

            // 3d
            -moz-transform-style: preserve-3d;
            -webkit-perspective: 500px;
            -webkit-transform: translateZ(0px);
            -moz-transform: translateZ(0px);
            transform: translateZ(0px);
        }

        &__card {
            position: absolute;
            width: 100%;
            height: 100%;
            left: 50%;
            top: 50%;
            transform: translate3d(-50%, -50%, 0);
            z-index: 2;
        }

        &__bg {
            position: absolute;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            left: 50%;
            top: 50%;
            width: 101%; // to hide borders
            height: 101%;
            border: 1px solid $color-white;
            border-radius: $border-radius;
            transform: translate(-50%, -50%);
            background-color: $color-silver;
            padding: toRem(30);
        }

        &__icon {
            opacity: 0;
            font-size: 50px;
        }
        &__caption {
            opacity: 0;
        }

        &__image {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            width: 427px;
            height: 600px;
            overflow: hidden;

            canvas {
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                width: $sequenceWidth;
                height: $sequenceHeight;
            }

            @include mq($until: tablet) {
                width: 100%;
                height: 100%;
            }

            &.is-desktop {
                @include mq($until: tablet) {
                    display: none;
                }
            }
            &.is-mobile {
                @include mq(tablet) {
                    display: none;
                }
            }

            img {
                @include object-fit(contain);
            }
        }
    }

    &__gold {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        padding: toRem(30);
        width: 427px;
        height: 600px;
        background-color: $color-gold;
        border: 1px solid $color-white;
        border-radius: $border-radius;
        transform: translate3d(-50%, -50%, -1px);
        z-index: 0;

        @include mq($until: mobile-landscape) {
            width: calc(100vw - 48px);
            height: 466px;
        }

        &__image {
            position: absolute;
            width: 100%;
            height: 500px;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);

            @include mq($until: mobile-landscape) {
                height: 350px;
            }
            img {
                @include object-fit(cover);
            }
        }

        &__icon {
            font-size: 50px;
        }
    }

    &__caption {
        display: flex;
        justify-content: space-between;
        flex-direction: row;
        padding-top: toRem(200);

        @include mq($until: tablet) {
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }

        &__uptitle,
        &__content {
            width: 50%;
            opacity: 0;

            @include mq($until: tablet) {
                width: 100%;
            }
        }

        &__uptitle {
            font-weight: 700;
            margin: 0;

            @include mq($until: tablet) {
                margin-bottom: toRem(60);
            }
        }
    }

    &__text {
        margin-bottom: toRem(80);

        @include mq($until: tablet) {
            margin-bottom: toRem(60);
        }
    }

    &__button {
        a {
            @include mq($until: 'mobile-landscape') {
                width: 100%;
            }
        }
    }
}
