.cursor {
    $block: &;

    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    width: 0;
    height: 0;
    margin: 0;
    transition: font-size 0.2s $ease, width 0.5s $ease-out-expo, height 0.5s $ease-out-expo,
        margin 0.2s $ease;
    pointer-events: none;
    mix-blend-mode: difference;

    @include mq($until: tablet-landscape) {
        display: none;
    }

    &.is-moved {
        width: 10px;
        height: 10px;
        margin: -5px 0 0 -5px;
    }

    // style when hovering needed item
    &.is-visible {
        width: 120px !important;
        height: 120px !important;
        margin: -60px 0 0 -60px !important;
        mix-blend-mode: normal;
    }

    &-move-outer {
        position: absolute;

        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    &-outer {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        background-color: #fff;
        border-radius: 50%;
        font-size: 0;
        box-sizing: border-box;
        transition: transform 0.4s $ease-out-quint, font-size 0.2s $ease;

        .is-visible & {
            font-size: $font-size-base;
        }
    }

    &__text {
        height: 28px;
        overflow: hidden;
        text-align: center;

        &-inner {
            line-height: 1.75;
            will-change: transform;
            font-size: 0;
            transition: all 0.4s $ease-out-expo;

            .is-visible & {
                font-size: $font-size-base;
            }

            .is-video-playing & {
                transform: translateY(-28px);
            }
            .is-team-hovered & {
                transform: translateY(-56px);
            }
        }
    }
}
