.faq {
    $block: &;

    background-color: $color-white;
    padding: toRem(350) 0 0;

    @include mq($until: mobile) {
        padding: toRem(150) 0;
    }

    &__title {
        margin-bottom: toRem(100);
        opacity: 0;

        @include mq($until: mobile) {
            margin-bottom: toRem(60);
        }
    }

    &__accordion {
        display: flex;
        flex-direction: column;
        border-bottom: 1px solid $color-silver;

        &__container {
            opacity: 0;
        }

        &__header {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: toRem(30) 0;
            cursor: pointer;

            &__title {
                letter-spacing: 0.05em;
                font-weight: 700;
            }

            &__icon {
                display: flex;
                justify-content: center;
                align-items: center;
                transition: all 0.4s $ease;
                pointer-events: none;
            }

            .is-open &__icon {
                transform: rotate(45deg);
            }
        }

        &__content {
            padding: toRem(40) 0;

            @include mq($until: mobile) {
                padding: toRem(32) 0;
            }

            &__wrapper {
                max-height: 0;
                transition: all 0.4s $ease;
                overflow: hidden;
            }

            &__text {
                line-height: 200%;
                letter-spacing: 0.05em;

                @include mq($until: mobile) {
                    line-height: 140%;
                }

                a {
                    color: $color-black;
                    text-decoration: underline;
                }
            }
        }
    }
}
