.buy {
    $block: &;

    position: relative;
    background-color: $color-black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    padding: 0;
    overflow-y: auto;

    @include mq($until: desktop) {
        overflow-y: scroll;
        justify-content: flex-start;
        padding: toRem(40) 0 toRem(15) 0;
    }

    &__wrapper {
        position: absolute;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 100%;
        width: 100%;
        transition: 0.2s $ease-out;
        transition-delay: 0.25s;
        z-index: 1;
        opacity: 1;

        .is-form-opened & {
            transition-delay: 0s;
            transform: none;
            opacity: 0;
            z-index: -1;
        }

        .is-form-success & {
            transition-delay: 0s;
            transform: none;
            opacity: 0;
            z-index: -1;
        }
    }

    .container {
        display: flex;
        justify-content: center;
        flex-direction: row;

        @include mq($until: desktop) {
            justify-content: flex-start;
            flex-direction: column;
        }
    }

    &__nav {
        position: absolute;
        left: toRem(40);
        margin: 0;
        opacity: 0;
        transform: translateY(-20px);
        transition: $transition-link;

        .is-visible & {
            transition-delay: 0.3s;
            transform: none;
            opacity: 1;
        }

        @include mq($until: desktop) {
            position: static;
            display: flex;
            justify-content: space-around;
            max-width: none;
            margin-bottom: toRem(60);
        }

        &__item {
            padding: 22px 20px;
            border: 1px solid transparent;
            border-radius: $border-radius;
            color: $color-grey-dark;
            transition: $transition-link;
            cursor: pointer;

            @include mq($until: desktop) {
                padding: 22px 26px;
            }
            @include mq($until: mobile) {
                padding: 16px 10px;
            }

            .no-touch &:hover {
                color: $color-white;
            }

            &.is-active {
                border-color: $color-white;
                color: $color-white;
                pointer-events: none;
            }
        }
    }

    &__content {
        width: 100%;
        margin-right: -17%;
        max-width: 945px;

        @include mq($until: desktop) {
            margin: auto;
        }

        #{$block}__item {
            display: flex;

            opacity: 0;
            transform: translateY(-20px);
            transition: $transition-link;

            .is-visible & {
                transition-delay: 0.2s;
                transform: none;
                opacity: 1;
            }

            @include mq($until: tablet) {
                flex-direction: column;
            }

            &__visual {
                position: relative;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: flex-end;
                padding: 30px;
                border: 1px solid $color-white;
                border-radius: $border-radius;
                min-width: 427px;
                min-height: 600px;
                margin-right: toRem(80);
                transition: $transition-link;

                @include mq($until: tablet) {
                    min-width: 327px;
                    min-height: 466px;
                    margin: auto;
                }
                @include mq($until: mobile) {
                    min-width: 0;
                    width: 100%;
                }

                &.is-genesis {
                    .is-genesis-send & {
                        pointer-events: none;
                    }
                }

                &.is-quantum {
                    .is-quantum-send & {
                        pointer-events: none;
                    }
                }

                &__image {
                    position: absolute;
                    bottom: 21%;
                    overflow: hidden;
                    width: 100%;
                    height: 350px;
                    margin: 0 auto;
                    transition: opacity 0.3s ease;

                    @include mq($until: tablet) {
                        height: 280px;
                    }

                    &:not(.is-active) {
                        opacity: 0;
                    }

                    &__inner {
                        @include fillout;
                    }

                    img {
                        @include object-fit(contain);
                        @include fillout;
                        transition: $transition-link;

                        &:last-child {
                            opacity: 0;
                        }
                    }
                }

                &__bottom {
                    display: none;
                    width: 100%;
                    margin-top: toRem(30);
                    justify-content: space-between;
                    align-items: center;

                    &.is-active {
                        display: flex;
                    }
                }

                &__icon {
                    display: flex;
                    color: $color-white;
                    font-size: 50px;
                    height: 50px;
                    transition: $transition-link;
                }

                &__button {
                    display: block;

                    @include mq($until: tablet) {
                        display: none;
                    }

                    &__container {
                        &--genesis {
                            background-color: $color-white;

                            .is-genesis-send & {
                                background-color: $color-grey-darker;
                                color: $color-white;
                                cursor: not-allowed;
                                pointer-events: none;
                            }
                        }

                        &--quantum {
                            background-color: $color-white;

                            .is-quantum-send & {
                                background-color: $color-grey-darker;
                                color: $color-white;
                                cursor: not-allowed;
                                pointer-events: none;
                            }
                        }
                    }

                    &__label {
                        &--genesis {
                            display: block;

                            .is-genesis-send & {
                                display: none;
                            }

                            &-send {
                                display: none;

                                .is-genesis-send & {
                                    display: block;
                                }
                            }
                        }

                        &--quantum {
                            display: block;

                            .is-quantum-send & {
                                display: none;
                            }

                            &-send {
                                display: none;

                                .is-quantum-send & {
                                    display: block;
                                }
                            }
                        }
                    }
                }

                .no-touch &:hover {
                    #{$block}__item__visual__image {
                        img {
                            &:first-child {
                                opacity: 0;
                            }

                            &:last-child {
                                opacity: 1;
                            }
                        }
                    }

                    #{$block}__item__visual__icon {
                        color: $color-black;
                    }
                }

                // MOBILE
                @include mq($until: tablet) {
                    #{$block}__item__visual__image {
                        img {
                            &:first-child {
                                opacity: 0;
                            }

                            &:last-child {
                                opacity: 1;
                            }
                        }
                    }
                    #{$block}__item__visual__icon {
                        color: $color-black;
                    }
                }

                // MODS
                &.is-quantum {
                    .no-touch &:hover {
                        background-color: $color-silver;
                    }

                    @include mq($until: tablet) {
                        transition-delay: 0.3s;
                        background-color: $color-silver;
                    }
                }

                &.is-genesis {
                    .no-touch &:hover {
                        background-color: $color-gold;
                    }

                    @include mq($until: tablet) {
                        transition-delay: 0.3s;
                        background-color: $color-gold;
                    }
                }
            }

            &__uptitle {
                line-height: 2;
            }

            &__caption {
                display: none;
                margin: 0;
                opacity: 0;
                transform: translateY(-20px);
                transition: $transition-link;

                // if overlay is visible
                .is-visible & {
                    // transition-delay: 0.3s;
                    transform: none;
                    opacity: 1;
                }

                &.is-active {
                    transition-delay: unset;
                }

                @include mq($until: tablet) {
                    margin: toRem(60) auto auto;
                }

                #{$block}__item__title {
                    margin-bottom: toRem(60);
                }

                &.is-active {
                    display: block;
                }
            }
        }

        &__button {
            display: none;
            width: auto;

            @include mq($until: tablet) {
                margin: toRem(95) 0 toRem(15);
                justify-content: center;

                &.is-active {
                    display: flex;
                }
            }

            a {
                width: 60%;

                @include mq($until: 'mobile-landscape') {
                    width: 100%;
                }
            }

            &__container {
                &--genesis {
                    padding: toRem(22) toRem(20);
                    background-color: $color-white;

                    .is-genesis-send & {
                        background-color: $color-grey-darker;
                        color: $color-white;
                        cursor: not-allowed;
                        pointer-events: none;
                    }
                }

                &--quantum {
                    padding: toRem(22) toRem(20);
                    background-color: $color-white;

                    .is-quantum-send & {
                        background-color: $color-grey-darker;
                        color: $color-white;
                        cursor: not-allowed;
                        pointer-events: none;
                    }
                }
            }

            &__label {
                &--genesis {
                    display: block;

                    .is-genesis-send & {
                        display: none;
                    }

                    &-send {
                        display: none;

                        .is-genesis-send & {
                            display: block;
                        }
                    }
                }

                &--quantum {
                    display: block;

                    .is-quantum-send & {
                        display: none;
                    }

                    &-send {
                        display: none;

                        .is-quantum-send & {
                            display: block;
                        }
                    }
                }
            }
        }
    }
}
