.about {
    $block: &;

    &__header {
        padding: toRem(160) 0 toRem(160);
        background-color: $color-black;

        @include mq($until: mobile) {
            padding: 0 0 toRem(150);
        }
    }

    &__footer {
        background-color: $color-brown;
        padding: toRem(100) 0 toRem(160);

        @include mq($until: mobile) {
            padding: toRem(150) 0;
        }
    }

    &__title {
        margin-bottom: toRem(60);
        text-align: left;
    }

    &__caption {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        transition: $transition-link;
        opacity: 0;

        .is-visible & {
            transition-delay: 0.25s;
            transform: none;
            opacity: 1;
        }

        &__image {
            padding: toRem(22) toRem(100) 0;
            font-size: 100px;
            display: flex;
            color: $color-white;
            transition: $transition-link;
            width: 50%;

            @include mq($until: tablet-landscape) {
                width: 100%;
                padding: toRem(80) 0;
                justify-content: center;
            }
        }
    }

    &__uptitle,
    &__text {
        width: 50%;
        letter-spacing: 0.05em;

        @include mq($until: tablet-landscape) {
            width: 100%;
        }
    }

    &__uptitle {
        font-weight: 700;
        margin-bottom: toRem(20);
    }

    &__text {
        .copy {
            line-height: 200%;
        }
        ul {
            margin-top: toRem(80);

            @include mq($until: tablet-landscape) {
                margin-top: toRem(60);
            }
        }
    }

    &__image {
        position: relative;
        width: 100%;
        border-radius: $border-radius;
        overflow: hidden;
        margin: toRem(160) 0 toRem(100);
        opacity: 0;
        transition: $transition-link;

        .is-visible & {
            transition-delay: 0.25s;
            transform: none;
            opacity: 1;
        }

        &::before {
            content: '';
            display: block;
            padding-bottom: 35.21%;

            @include mq($until: notebook) {
                padding-bottom: 50%;
            }
            @include mq($until: tablet-landscape) {
                padding-bottom: 80%;
            }
        }

        &:nth-child(4) {
            margin: toRem(160) 0 toRem(100);
        }

        &:nth-child(6) {
            display: none;
        }

        &:nth-child(7) {
            display: none;
        }

        @include mq($until: mobile) {
            &:nth-child(2) {
                margin: toRem(150) toRem(10) toRem(125);
            }

            &:nth-child(4) {
                margin: toRem(150) toRem(10) toRem(5);
            }

            &:nth-child(6) {
                display: block;
                margin: toRem(5) toRem(10);
            }

            &:nth-child(7) {
                display: block;
                margin: toRem(5) toRem(10) 0;
            }
        }

        img {
            @include fillout;
            @include object-fit(cover);
        }
    }
}
