.news {
    $block: &;

    padding: toRem(170) 0;
    transform: translate(0px, 350px);
    z-index: 10; //to overlay footer
    border-radius: $border-radius;
    overflow: hidden;

    @include mq($until: tablet) {
        padding: toRem(80) 0;
    }

    &__bg {
        position: absolute;
        left: 50%;
        top: 0;
        transform: translateX(-50%);
        width: 95%;
        height: 100%;
        background-color: $color-brown;
        border-radius: $border-radius;
        margin: auto;
    }

    &__inner {
        opacity: 0;
    }

    &__figure {
        display: flex;
        margin-bottom: 0;
        transform: translate(10%, 50%);

        @include mq(tablet) {
            position: absolute;
            bottom: 50%;
            right: 10%;
        }
        @include mq($until: tablet) {
            position: relative;
            z-index: 10;
            justify-content: center;
            margin-bottom: toRem(60);
            transform: none;
        }
    }

    &__title {
        margin-bottom: toRem(80);
        z-index: 1;
        max-width: toRem(563);

        @include mq($until: tablet) {
            margin-bottom: toRem(60);
        }

        &__wrapper {
            display: flex;
            z-index: 1;
        }
    }

    &__button {
        z-index: 1;
        display: flex;
        justify-content: flex-start;

        @include mq($until: tablet) {
            justify-content: center;
        }

        a {
            @include mq($until: mobile) {
                width: 100%;
            }
        }
    }
}
